<template>
	<div class="pdawrap">
		<div class="search_out">
		<!-- 钢网架编码 -->
			<div class="choose_div">
				<span class="searchout_title">{{$t('storage.shelf_id')}}:</span>
				<template>
			  	<el-select value-key="shelf_id" v-model="shelf_id">
			    	<el-option
			    		change
			      	v-for="item in shelfList"
			      	:key="item"
			      	:label="item"
			      	:value="item">
			    	</el-option>
			  	</el-select>
				</template>
			</div>
			<!-- 钢网条码 -->
			<div class="pda_selected_div">
				<span class="searchout_title" >{{$t('storage.label_code')}}:</span>
				<el-input v-model="label_code" clearable @keyup.enter.native ="takeIn"></el-input>
			</div>
			<p class="pdaintips" v-show="showinputtips">{{$t('pda.pdaintips')}}</p>
			<div class="takeIn">
				<el-button type="primary" @click="takeIn" :disabled="isDisabled?true:false">{{$t('pda.take_in')}}</el-button>
			</div>
		</div>
		<div class="table_div" v-if="mate_list">
			<table class="labeltable">
				<tr class="label_tr">
          <td class="title_td">{{$t('storage.label_code')}}</td>
          <td class="content_td">{{this.mate_list.label_code}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.customer_name')}}</td>
          <td class="content_td">{{this.mate_list.customer_name}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.supplier_name')}}</td>
          <td class="content_td">{{this.mate_list.supplier_name}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.steel_model')}}</td>
          <td class="content_td">{{this.mate_list.steel_model}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.version_num')}}</td>
          <td class="content_td">{{this.mate_list.version_num}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.shelf_id')}}</td>
          <td class="content_td">{{this.mate_list.shelf_id}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.position')}}</td>
          <td class="content_td">{{this.mate_list.position}}</td>
        </tr>
			</table>	
		</div>
	</div>
</template>
<script>
 import { onlineShelfList } from '@/request/api/materialshelf'
 import { mateIn, lastInputMate } from '@/request/api/material'
	export default {
		data() {
			return {
				shelfList:[],
				shelf_id:'',
				isDisabled:false,
				showinputtips:false,
				label_code:'',
				mate_list:''
			}
		},
		mounted() {
			this.getShelf()
		},
		methods:{
			getShelf() {
				onlineShelfList({}).then(res =>{
					if (res.result) {
						this.shelfList = res.data
					} else {
						this.$message({
	          	showClose: true,
	          	message: res.msg,
	          	type: 'error'
	        	})
					}
				})
			},
			// typeChange(shelf_id){
			// 	this.shelf_id = shelf_id
			// },
			takeIn(){
				let _this = this
				this.isDisabled = true
				if (_this.shelf_id&&_this.label_code) {
					this.showinputtips = false
					let data = {
						label_code:this.label_code,
						shelf_id:this.shelf_id
					}
					mateIn(data).then(res =>{
						this.mate_list = ''
						if (res.result) {
							this.longSearch(0,60,data)
						} else {
							this.$message({
								showClose:true,
								message:res.msg,
								type:'error'
							})
						}
					})
				} else {
					this.showinputtips = true
				}					
				let Timer=window.setTimeout(function(){
					_this.isDisabled = false
				},1500)
			},
			// 轮询
			longSearch(i,times,data) {
				let self = this
				setTimeout(function(){
					lastInputMate(data).then(res =>{
						if (res.result) {
							self.label_code = ''
							self.mate_list = res.mate_info
							self.$message({
								showClose:true,
								message:self.$t('pda.takein_success'),
								type:'success'
							})
						} else {
							if(i++<times){
								self.longSearch(i,times,data)
								if(i==60){
									self.$message({
										showClose:true,
										message:self.$t('pda.takein_failed'),
										type:'error'
									})
								}
							}
						}
					})
					
				},500)
			}

		}
	}
</script>
<style scoped>
	.labeltable {
  border-collapse: collapse;
  margin: 0 auto;
  text-align: center;
}
.table_div{
	margin-top: 0.53rem;
}
	.labeltable td{
		border:0.026rem solid lightgray;
		height: 0.85rem;
		line-height: 0.85rem;
	}
	.table_div .title_td{
		width:40%;
	}
	.table_div .content_td{
		width:60%;
	}
	.takeIn .el-button{
		width:100%;
	}
	.pdaintips{
		height: 0.64rem;
		line-height: 0.64rem;
		color: red;
		font-size: 0.4rem;
		text-align: left;
	}
</style>


